"use client";

import { useState } from "react";
import {
  Box,
  Button,
  Heading,
  Icon,
  Image,
  SimpleGrid,
  Text,
  VStack,
  useColorModeValue,
} from "@chakra-ui/react";
import { StarIcon, InfoIcon, AtSignIcon } from "@chakra-ui/icons";
import AnimatedSection from "../../components/layout/AnimatedSection";

const involvementOptions = [
  {
    title: "Parents & Families",
    description:
      "Walk alongside your student in STEM. Help with transportation, meals, event support, and spreading the word so more students can access high-quality STEM opportunities.",
    icon: StarIcon,
  },
  {
    title: "Mentors & Volunteers",
    description:
      "Share your experience in engineering, technology, or education and mentor the next generation. Whether you're a FIRST alum, student, or professional, your time has a direct impact.",
    icon: InfoIcon,
  },
  {
    title: "Sponsors & Partners",
    description:
      "Invest in tools, materials, and experiences that unlock STEM for underrepresented students. As a 501(c)(3), we work with companies, foundations, and individuals to build long-term impact.",
    icon: AtSignIcon,
  },
];

const photoSlides = [
  {
    src: "/images/get-involved/2022-team-photo.jpg",
    alt: "Team photo from 2022",
    caption: "Team 8732 & 10312 celebrating after competition",
  },
  {
    src: "/images/get-involved/library-diaz.jpg",
    alt: "Students collaborating at the library",
    caption: "Students mentoring peers during a library outreach event",
  },
  {
    src: "/images/get-involved/plano-award.JPEG",
    alt: "Team receiving an award",
    caption: "Recognition for community impact and STEM outreach",
  },
  {
    src: "/images/get-involved/elevator-trap.jpg",
    alt: "Robotics build session",
    caption: "Hands-on build session during the season",
  },
  {
    src: "/images/get-involved/itkan-practice.JPEG",
    alt: "Drive team practice",
    caption: "Drive team practice preparing for the next regional",
  },
];

export default function GetInvolvedPage() {
  const [currentSlide, setCurrentSlide] = useState(0);
  const headingColor = useColorModeValue("text.light", "text.dark");
  const subTextColor = useColorModeValue("text.mutedLight", "text.mutedDark");
  const cardBg = useColorModeValue("surface.light", "surface.dark");
  const cardBorder = useColorModeValue("blackAlpha.100", "whiteAlpha.200");
  const cardText = useColorModeValue("text.mutedLight", "text.mutedDark");
  const accentColor = useColorModeValue("accent.500", "accent.400");

  return (
    <>
      <AnimatedSection variant="none" py={{ base: 16, md: 24 }}>
        <VStack spacing={4} textAlign="center">
          <Heading
            as="h1"
            size={{ base: "2xl", md: "3xl" }}
            color={headingColor}
          >
            Get Involved
          </Heading>
          <Text
            fontSize={{ base: "lg", md: "xl" }}
            maxW="3xl"
            color={subTextColor}
          >
            Join us in making a difference. Whether you're a parent, mentor, or
            sponsor, there are many ways to contribute to our mission.
          </Text>
        </VStack>
      </AnimatedSection>

      <AnimatedSection variant="alt">
        <VStack spacing={6}>
          <VStack spacing={2} textAlign="center">
            <Heading as="h2" size="xl" color={headingColor}>
              Moments From Our Teams
            </Heading>
            <Text color={subTextColor} maxW="3xl">
              A glimpse into practices, outreach, and competitions that your
              support makes possible.
            </Text>
          </VStack>

          <Box
            position="relative"
            w="100%"
            maxW="4xl"
            mx="auto"
            overflow="hidden"
            borderRadius="xl"
            borderWidth="1px"
            borderColor={cardBorder}
            shadow="xl"
            bg={cardBg}
          >
            <Image
              src={photoSlides[currentSlide].src}
              alt={photoSlides[currentSlide].alt}
              w="100%"
              h={{ base: "240px", md: "360px" }}
              objectFit="cover"
              fallbackSrc="/images/get-involved/2022-team-photo.jpg"
            />

            <Box
              px={{ base: 4, md: 6 }}
              py={3}
              bg="blackAlpha.600"
              color="white"
            >
              <Text fontWeight="semibold">
                {photoSlides[currentSlide].caption}
              </Text>
            </Box>

            <Box
              position="absolute"
              top="50%"
              left={0}
              right={0}
              px={4}
              display="flex"
              justifyContent="space-between"
              transform="translateY(-50%)"
              pointerEvents="none"
            >
              <Button
                pointerEvents="auto"
                size="sm"
                variant="solid"
                onClick={() =>
                  setCurrentSlide((prev) =>
                    prev === 0 ? photoSlides.length - 1 : prev - 1
                  )
                }
              >
                Prev
              </Button>
              <Button
                pointerEvents="auto"
                size="sm"
                variant="solid"
                onClick={() =>
                  setCurrentSlide((prev) =>
                    prev === photoSlides.length - 1 ? 0 : prev + 1
                  )
                }
              >
                Next
              </Button>
            </Box>
          </Box>

          <Box display="flex" justifyContent="center" gap={2}>
            {photoSlides.map((_, idx) => (
              <Box
                key={idx}
                w={3}
                h={3}
                borderRadius="full"
                bg={idx === currentSlide ? "accent.500" : "blackAlpha.300"}
              />
            ))}
          </Box>
        </VStack>
      </AnimatedSection>

      <AnimatedSection variant="default">
        <VStack spacing={10}>
          <VStack spacing={3} textAlign="center">
            <Heading as="h2" size="xl" color={headingColor}>
              How You Can Help
            </Heading>
            <Text color={subTextColor} maxW="3xl">
              Choose the path that fits you best. Every role supports students
              discovering what's possible through STEM.
            </Text>
          </VStack>

          <SimpleGrid columns={{ base: 1, md: 3 }} spacing={6} w="100%">
            {involvementOptions.map((option) => (
              <VStack
                key={option.title}
                align="flex-start"
                spacing={4}
                p={6}
                bg={cardBg}
                borderRadius="lg"
                borderWidth="1px"
                borderColor={cardBorder}
                shadow="lg"
              >
                <Icon as={option.icon} boxSize={10} color={accentColor} />
                <Heading as="h3" size="lg" color={headingColor}>
                  {option.title}
                </Heading>
                <Text color={cardText}>{option.description}</Text>
              </VStack>
            ))}
          </SimpleGrid>
        </VStack>
      </AnimatedSection>

      <AnimatedSection variant="alt">
        <Box
          w="100%"
          maxW="3xl"
          mx="auto"
          bg={cardBg}
          borderRadius="xl"
          borderWidth="1px"
          borderColor={cardBorder}
          shadow="xl"
          p={{ base: 6, md: 8 }}
        >
          <VStack spacing={6} align="stretch">
            <VStack spacing={2} textAlign="center">
              <Heading as="h2" size="lg" color={headingColor}>
                Contact Us
              </Heading>
              <Text color={subTextColor}>
                Ready to get involved? Fill out our Google Form and we'll be in
                touch.
              </Text>
            </VStack>

            <Button
              as="a"
              href="https://forms.gle/N9xeWS7eBrRYzWuo9"
              target="_blank"
              rel="noopener noreferrer"
              colorScheme="brand"
              size="lg"
              w="100%"
            >
              Open the Google Form
            </Button>
          </VStack>
        </Box>
      </AnimatedSection>
    </>
  );
}
